<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Edit Profile</h1>
      <div class="chartWrap">
        </div>
    </section>
    <div class="formWrap">
        <van-field label="Name" :placeHolder='user.nickname'  v-model="form.nickname" />
        <div class="sex" @click="showName()">
            <van-field label="Gender" disabled right-icon="arrow-down" :placeHolder="user.sex==1?'Man':'Woman'"  v-model="gender"  />
            <div class="witch" v-show="control">
                <p @click.stop="selectGender(1)">Man</p>
                <p @click.stop="selectGender(2)">Women</p>
            </div>
        </div>
        
        <p class="tips">You can change it only one time. It will be visible on Shopeone</p>
        <van-field label="User Name" :placeHolder='user.userName' v-model="form.userName" />
        <div class="email">
            <van-field label="Email" readonly="readonly" :placeHolder='user.email'  />
              <span class="change" @click="$router.push({path:'editEmailOrMobile',query:{accountType:1}})">{{user.email?'Change':'Add'}}</span>
        </div>
         <div class="email">
            <van-field label="Mobile" readonly="readonly"  :placeHolder='user.telephone' />
            <span class="change" @click="$router.push({path:'editEmailOrMobile',query:{accountType:2}})">{{user.telephone?'Change':'Add'}}</span>
        </div>
    
        <van-button type="danger" @click="submitEdit">Update Profile</van-button>
        <span class="reset" @click="$router.push('resetPwd')">Reset Password</span>
         <span class="reset" v-if="user.level==1" @click="$router.push('becomeVip')">Becom Cooperative VIP</span>
    </div>
    <div class="mengban" v-show="control" @click="control=false">

    </div>
</div>
</template>

<script>
import {mapGetters} from 'vuex'
import {editUser} from '@/api/user'

export default {
  name: 'editProfile',
  data () {
    return {
        value:"",
         control: false,
        form:{
            userName:"",
            nickname:"",
            sex:"",
        },
            gender:""
    }
  },
  computed: {
      ...mapGetters(['user'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    showName(){
        this.control = true;
    },
    submitEdit(){
        this.form.id=this.user.id;
        console.log(this.user)
        this.form.userName = this.form.userName? this.form.userName : this.user.userName;
        this.form.nickname = this.form.nickname? this.form.nickname : this.user.nickname;
        editUser(this.form).then(res=>{
            //刷新页面，重新加载用户信息
            this.$store.dispatch("userInfo").then(r => {});
            this.$router.push('center');
        });
    },
    selectGender(sex){
        this.form.sex=sex;
        this.gender=this.form.sex==1?'Man':'Woman';
        this.control=false;
    }

  }
}
</script>

<style lang="scss" scoped>
   .formWrap{
       width: 100%;
       padding-right: 4%;
       border-top: 10px solid #f6f6f6;
       .van-cell{
            padding: 10px 15px 10px 15px;
            color: #999999;
        }
       .coat{
           width: 100%;
           padding: 0 0 0 4%;
           display: -webkit-box;
           border-bottom: #f6f6f6 1px solid;
           .label{
               color: #999999;
               display: flex; align-items: center;
           }
           .codeName{
               display: flex; align-items: center;
               padding-right: 10px;
           }
           .van-cell{
                   padding: 10px 0 10px 15px;
                   width: 60%;
            }
            .add{
                width: 8%;
                display: flex; align-items: center;
                color: #2B87D5;
            }      
       }
       p{
           font-size: 10px;
           color: #666666;
           padding: 5px 0 0 15px;
       } 
       .tips{
           font-size: 11px;
           color: #666666;
       }
       .email{
           width: 100%;
           display: -webkit-box;
           .van-cell{
               width: 85%;
           }
           .change{
               width: 15%;
               display: flex; align-items: center;justify-content: flex-end;
               color: #2B87D5;
               border-bottom: #f6f6f6 1px solid;
           }
       }
   }
   
   .van-cell:nth-child(2){
       padding-top: 0;
       color: #333333;
   }
   .van-cell:nth-child(3){
       padding-right: 0;
   }
   .van-cell:nth-child(5){
       padding-top: 0;
   }
   .van-button--danger{
        width: 96%;
        margin-left: 4%;
        margin-top: 65px;
    }
    .reset{
        width: 96%;
        display: block;
        margin-left: 4%;
        color: #2B87D5;
        font-size: 12px;
        text-align: center;
        padding-top: 10px;
    }
    .sex{
        width: 100%;
        position: relative;
        .witch{
            position: absolute;
            right: 20px; 
            background: #fff; 
            border-radius: 5px; 
            box-shadow: 0 0 5px 2px rgba(23,23,23,.2); 
            padding: 5px 5px 10px; 
            z-index: 1000;
            height: 90px;
            p{
                padding:10px 10px; 
                text-align: center; 
                white-space: normal; 
                min-width: 120px;
                font-size: 14px;
            }
        }
    }
.mengban{
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 111;
    background-color: rgba(0, 0, 0, .5);
    top: 0;

}
</style>






